import React from "react";

import { Card, CardColumns } from 'react-bootstrap';

// REMINDER!!
// This all still needs tidied up. It's just a boilerplate for now.
// I'll probably split out CardColumns into its own component and inject the cards.

class NewsCards extends React.Component {
	render() {
		return (
			<div>
				<h2>{this.props.title}</h2>			
				<CardColumns>
					<Card>
						<Card.Img variant="top" src="https://via.placeholder.com/356x160" />
						<Card.Body>
							<Card.Title>Card title that wraps to a new line</Card.Title>
							<Card.Text>
								This is a longer card with supporting text below as a natural
								lead-in to additional content. This content is a little bit
								longer.
							</Card.Text>
						</Card.Body>
					</Card>
					<Card className="p-3">
						<blockquote className="blockquote mb-0 card-body">
							<p>
								Lorem ipsum dolor sit amet, consectetur adipiscing elit. Integer
								posuere erat a ante.
							</p>
							<footer className="blockquote-footer">
								<small className="text-muted">
									Someone famous in <cite title="Source Title">Source Title</cite>
								</small>
							</footer>
						</blockquote>
					</Card>
					<Card bg="primary" text="white" className="text-center p-3">
						<blockquote className="blockquote mb-0 card-body">
							<p>
								Lorem ipsum dolor sit amet, consectetur adipiscing elit. Integer
								posuere erat a ante.
							</p>
							<Card.Footer>
								<small>
									Someone famous in <cite title="Source Title">Source Title</cite>
								</small>
							</Card.Footer>
						</blockquote>
					</Card>
					<Card className="text-center">
						<Card.Body>
							<Card.Title>Card title</Card.Title>
							<Card.Text>
								This card has supporting text below as a natural lead-in to
								additional content.{" "}
							</Card.Text>
							<Card.Text>
								<small className="text-muted">Last updated 3 mins ago</small>
							</Card.Text>
						</Card.Body>
					</Card>
					<Card>
						<Card.Img variant="top" src="https://via.placeholder.com/356x160" />
						<Card.Body>
							<Card.Title>Card title</Card.Title>
							<Card.Text>
								This card has supporting text below as a natural lead-in to
								additional content.{" "}
							</Card.Text>
						</Card.Body>
						<Card.Footer>
							<small className="text-muted">Last updated 3 mins ago</small>
						</Card.Footer>
					</Card>
					<Card>
						<Card.Body>
							<Card.Title>Card title</Card.Title>
							<Card.Text>
								This is a wider card with supporting text below as a natural
								lead-in to additional content. This card has even longer content
								than the first to show that equal height action.
							</Card.Text>
							<Card.Text>
								<small className="text-muted">Last updated 3 mins ago</small>
							</Card.Text>
						</Card.Body>
					</Card>
				</CardColumns>
			</div>
		);
	}
}

export default NewsCards;
